@use '../base/utilities' as *;

// main hero
.hero {
    // colors. using css variable due to some limitations with scss variables
    --color-primary: hsl(184, 75%, 38%);
    --color-primary-hue: 184;
    --color-primary-saturation: 75%;
    --color-primary-lightness: 50%;

    position: relative;
    min-height: 1200px;
    // background-color: aquamarine;

    .heroImage {
        position: sticky;
        top: 0px;
        margin: auto;
        height: 800px;
        width: 100%;
        background-image: url('../../assets/images/hero.jpg');
        background-repeat: no-repeat;
        background-position: center center;
        background-size: cover;
        z-index: -1;
    }

    .content {
        position: sticky;
        bottom: 40px;

        margin-left: 230px;
        padding: 30px;
        color: var(--color-primary);
        overflow: hidden;
        display: inline-block;
        width: 500px;

        h1 {
            max-width: 407px;
            @include textShadow(5px, hsl(0, 0%, 45%));
        }

        h3 {
            @include textShadow(8px, hsl(0, 0%, 45%));

        }

        .cta-btn {
            margin-top: 30px;

        }

        button {
            padding: 10px 30px;
            border-radius: 30px;
            color: var(--color-primary);
            font-weight: 400;
            font-size: toRem(16);
            background-color: transparent;
            border: none;
            box-shadow: 1px 1px 3px 1px hsl(var(--color-primary-hue), var(--color-primary-saturation), var(--color-primary-lightness));
        }

        button:hover {
            background-color: var(--color-primary);
            color: #eee;

        }

        button:nth-child(2) {
            --color-primary-lightness: 30%;
            margin: 0 10px;
            background-color: var(--color-primary);
            color: #eee;

            &:hover {
                --color-primary-lightness: 50%;
                background-color: transparent;
                color: var(--color-primary);
            }
        }

    }

    @media(max-width: 768px) {

        .content {
            bottom: 35vh;
            margin-left: 0;
        }
    }
}


// slider

.sliderWrapper {
    position: sticky;
    bottom: 100px;
    padding-bottom: 20px;

    z-index: -10;
    overflow: hidden;
    width: 100%;
    // background-color: aqua;
}
.sliderContainer {
    display: flex;
    margin: 100px auto;
    rotate: -8.2deg;
    width: 120%;
    
    .slider {
        display: flex;
        animation: slider 20s linear 0s infinite;
    }
    
    .slide {   
        margin: 0 5px;
        overflow: hidden;
    }
    .txt {
        $color-primary: hsl(184, 75%, 38%);
        font-size: 64px;
        font-family: 'Kalam', cursive;
        white-space: nowrap;
        color: $color-primary;
    }

    @keyframes slider {
        100% {
            transform: translateX(-100%);
        }

    }
}